.breed-select {
  width: 100%;
  max-width: 300px;
  margin: 10px auto;
  padding: 10px;
  font-family: Arial, sans-serif;
  font-size: 16px;
}

.loader {
  width: 50px;
  height: 50px;
  border: 5px solid #ccc;
  border-top-color: #333;
  border-radius: 50%;
  margin: 20px auto;
  animation: spin 1s linear infinite;
  display: block;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.error {
  color: #f44336;
  text-align: center;
  margin-top: 20px;
  font-size: 18px;
  font-weight: bold;
  display: none;
}

.cat-info {
  text-align: center;
  margin-top: 20px;
  display: none;
}

.cat-info img {
  max-width: 100%;
  height: auto;
}

